import { Component, OnInit } from '@angular/core';
import { ApicallService } from 'src/app/services/CallApi/apicall.service';
import { GlobalService } from 'src/app/services/data/global.service';
import { format, parseISO } from 'date-fns';
import { ToastService } from 'src/app/services/toasts/toast.service';
import { NgxPrintElementService } from 'ngx-print-element';
@Component({
  selector: 'app-see-accu-data',
  templateUrl: './see-accu-data.page.html',
  styleUrls: ['./see-accu-data.page.scss'],
})
export class SeeAccuDataPage implements OnInit {

  filterTerm!: string;
  public Account_details:any;
  public totalnetBalance:any= 0;
  public totalDebit:any = 0;
  public totalCredit:any = 0;
  show = false;
  public postDate:any = {start:'', end:''};
  public postAlltypes:any = {type:'', stype:'', source:''};
  public fromDate : any;
  public toDate : any;
  // public type :any = 'slectType';
  public slectType: any;
  public slectStype: any;
  public slectSource: any;
  public selectdate: any;
  public alltypeData: any;
  public selectTabs:any = 'Bank';
  public maine_dene_hain: any = 0;
  public maine_lene_hain: any = 0;
  public allkhataData: any;
  public owners: any;
  public type: any;
  public slectOwner: any;
  public transaction:any = {a_id:'', type:''}
  public expences:any = {a_id:'', type:'', description:''}
  public style = {
    'tr': {
      border: '1px solid black',
    }
  };
 public totalDebit_main: any;
 public totalCredit_main: any;
 public totalnetBalance_main: any;
 public all_data_main: any;
 public totalDebit2: any;
  constructor(public apicall: ApicallService, public global : GlobalService, public toast : ToastService, public print: NgxPrintElementService) { }

 async ngOnInit() {

   await this.apicall.api_getOwner();
    await this.global.Owner.subscribe( owner => {
      console.log(owner);
      this.owners = owner[0];
      console.log(this.owners);
    });

    await this.apicall.Get_alltransaction(this.owners.a_id);

    await  this.global.Alltransactions.subscribe(res =>{
      this.all_data_main = res[0];
      console.log(res);
      console.log(this.all_data_main);

      this.totalnetBalance_main = res[0];
      this.totalDebit_main = res[1];
      this.totalCredit_main = res[2];
      console.log(this.totalnetBalance_main);      
      console.log(this.totalDebit_main);      
      console.log(this.totalCredit_main);      
  });
  }



      //  Select Owner
  selectFrom($event) {
    console.log($event.target.value);
    this.transaction.a_id = $event.target.value.id;
    this.expences.a_id = $event.target.value.id;
    console.log(this.transaction)

  }

  // Select Account Transaction Type
 async selectTypeTransaction($event) {
    console.log($event.target.value);
    this.type = $event.target.value;
    if($event.target.value == 'transaction'){
      this.transaction.type = $event.target.value;
     await this.apicall.api_gettransactionsbymainaccount(this.transaction);
    await this.getalltranstions();
    }
    if($event.target.value == 'asset'){
      this.transaction.type = $event.target.value;
     await this.apicall.api_gettransactionsbymainaccount(this.transaction);
    await this.getalltranstions();
    }
    if($event.target.value == 'installments'){
      this.transaction.type = $event.target.value;
     await this.apicall.api_gettransactionsbymainaccount(this.transaction);
    await this.getalltranstions();
    }
    if($event.target.value == 'bookings'){
      this.transaction.type = $event.target.value;
     await this.apicall.api_gettransactionsbymainaccount(this.transaction);
    await this.getalltranstions();
    }
    else{
      console.log('please select correct type');
      this.expences.type = $event.target.value;
    }
  }

  async getbyExpense($event) {
    console.log($event.target.value);
    this.expences.description = $event.target.value;
    console.log(this.expences);
    await this.apicall.api_gettransactionsbyexpense(this.expences);
    await this.getalltranstions();
  }

  // all balance by select value
   getalltranstions(){
    this.global.Alltransactions.subscribe(res =>{
      this.alltypeData = res[0];
      console.log(res[0]);
      console.log(this.alltypeData);
    if(res[0] == ''){
      console.log('sorry no data found ');
      this.toast.nodatafound();
    }
    else{
      this.Account_details = res[0];
      this.totalDebit = res[1][0];
      this.totalCredit = res[2][0];
      this.totalnetBalance = res[3][0];
      console.log(this.Account_details);      
      console.log(this.totalCredit);      
      console.log(this.totalDebit);      
      console.log(this.totalnetBalance); 
    }   

  });
  }

  // all balance without select value top
  //  gettransactions_all(){

  // }


 async getAllAccountsData(){
   await this.apicall.api_get_all_accounts();
    this.global.StoreAllaccounts.subscribe(res =>{
        this.alltypeData = res[0];
      if(res[0] == ''){
        console.log('sorry no data found ');
        this.toast.nodatafound();
      }
      else{
        this.Account_details = res[0];
        this.totalDebit = res[1][0];
        this.totalCredit = res[2][0];
        this.totalnetBalance = res[3][0];
        console.log(this.Account_details);      
        console.log(this.totalCredit);      
        console.log(this.totalDebit);      
        console.log(this.totalnetBalance); 
      }   

    });
  }

  async getbyType($event) {
    console.log($event.target.value);
    this.postAlltypes.type = $event.target.value;
    console.log(this.postAlltypes.type);
    if($event.target.value  == 'all'){
      await this.apicall.api_get_all_accounts();
    }
    else{
      await this.apicall.api_getaccountsBytype($event.target.value)
    }
  }

  async getbyStype($event) {
    console.log($event.target.value);
    this.postAlltypes.stype = $event.target.value;
    console.log(this.postAlltypes.stype);
    if($event.target.value  == 'all'){
      await this.apicall.api_get_all_accounts();
    }
    else{
    await this.apicall.api_getaccountsBystype($event.target.value)
    }
  }

  async getbySource($event) {
    console.log($event.target.value);
    const x = $event.target.value;
    this.postAlltypes.source = $event.target.value;
    console.log(this.postAlltypes.source);
    // await this.apicall.api_getaccountsByalltype(this.postAlltypes);
    if($event.target.value  == 'all'){
      await this.apicall.api_get_all_accounts();
    }
    else{
    await this.apicall.api_getaccountsBysource($event.target.value)
    }
  }

  async getAlltypeaccount(){
    console.log(this.postAlltypes);
     await this.apicall.api_getaccountsByalltype(this.postAlltypes);
  }


  async refresh(){
    this.show= true;
    setTimeout(() => {
      this.show = false;
      // this.apicall.api_get_all_accounts();
      const x = [];
      this.global.store_alltransactions(x);
      this.totalDebit = '';
      this.totalCredit = '';
      this.totalnetBalance = '';
      this.toDate = '';
      this.fromDate = '';
      this.postDate.start = '';
      this.postDate.end = '';
      this.slectType = '';
      this.slectOwner = '';
      this.slectStype = '';
      this.slectSource = '';
      this.selectdate = '';
    }, 1500 ); 
  }

  segmentChanged(ev: any) {
    console.log('Segment changed', ev);
  }

  formatDate(value: string) {
    console.log(value);
    this.fromDate = format(parseISO(value), 'yyyy-MM-dd');
    console.log(this.fromDate);
    this.postDate.start = this.fromDate;
  }
  todate(toDate: string) {
    console.log(toDate);
    this.toDate = format(parseISO(toDate), 'yyyy-MM-dd');
    console.log(this.toDate);
    this.postDate.end = this.toDate;
    this.apicall.api_getaccountsBydate(this.postDate);
  }

  async getKhata(){
    await this.apicall.api_getallkhata();
    this.global.Allkhata.subscribe( khata =>{
      console.log(khata);
      this.allkhataData = khata[0];
      this.maine_lene_hain= khata[1][0];
      this.maine_dene_hain = khata[2][0];
    });
  }

}
